.header {
  width: 100%;
  height: 130px;
  background-color: var(--top-color);
  display: flex;
}

.header__image {
  width: 75px;
  height: 75px;
  margin: 25px;
}

.header__title {
  color: #fff;
  align-self: flex-end;
  margin-left: 25px;
}

:root {
  --left-color: #1db954;
  --top-color: #191414;
}

html, body {
  margin: 0;
  padding: 0;
}

body {
  overflow-x: hidden;
  overflow-y: scroll;
}

.wrap {
  max-width: 100%;
  margin: 0 auto;
}

.wrap-middle {
  width: 100%;
  font-size: 0;
  display: flex;
  position: relative;
}

.footer {
  height: 200px;
  width: 100%;
  background-color: var(--top-color);
  color: #fff;
}

.footer__title {
  margin-top: 0;
}

.footer__columns {
  flex-direction: column;
  align-items: flex-end;
  margin-right: 30px;
  display: flex;
}

.footer__column {
  font-size: 11px;
  display: flex;
}

.footer__column:hover {
  cursor: pointer;
  opacity: .7;
}

.footer__contact {
  width: 15px;
  height: 15px;
  align-self: center;
  margin-right: 5px;
}

.left {
  height: 625px;
  background-color: var(--left-color);
  width: 200px;
  font-size: .9rem;
  transition: transform .3s;
  display: inline-block;
  position: absolute;
  left: -200px;
}

.left__nav {
  flex-direction: column;
  gap: 50px;
  list-style: none;
  display: flex;
}

.left__nav-item {
  height: 25px;
  width: 25px;
  margin-top: 15px;
}

.left__nav-item:hover {
  cursor: pointer;
  background-color: #fff;
  border-radius: 50px;
  transition: all .25s ease-in-out;
  animation: .1s shake;
}

@keyframes shake {
  0% {
    transform: translate(1px, 1px)rotate(0);
  }

  10% {
    transform: translate(-1px, -2px)rotate(-1deg);
  }

  20% {
    transform: translate(-3px)rotate(1deg);
  }

  30% {
    transform: translate(3px, 2px)rotate(0);
  }

  40% {
    transform: translate(1px, -1px)rotate(1deg);
  }

  50% {
    transform: translate(-1px, 2px)rotate(-1deg);
  }

  60% {
    transform: translate(-3px, 1px)rotate(0);
  }

  70% {
    transform: translate(3px, 1px)rotate(-1deg);
  }

  80% {
    transform: translate(-1px, -1px)rotate(1deg);
  }

  90% {
    transform: translate(1px, 2px)rotate(0);
  }

  100% {
    transform: translate(1px, -2px)rotate(-1deg);
  }
}

@media (min-width: 40rem) {
  .left {
    width: 20.8333%;
    position: relative;
    left: 0;
  }
}

.middle {
  height: 625px;
  background-color: var(--top-color);
  color: #fff;
  font-size: .9rem;
  transition: transform .3s;
}

@media (min-width: 40rem) {
  .middle {
    width: 68.75%;
    display: inline-block;
  }
}

.right {
  height: 625px;
  width: 100%;
  background-color: var(--left-color);
  font-size: .9rem;
}

@media (min-width: 40rem) {
  .right {
    width: 10.4167%;
    display: inline-block;
  }
}



